.image{
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    z-index: 0;
    background-position: center center;
    background-size: cover;
    background-attachment: fixed;
}
@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@700&family=Roboto:wght@500;700&display=swap');
html,body,h1{
    font-family: 'Montserrat', sans-serif;
    font-family: 'Roboto', sans-serif;
}
h1{
    font-size: 100px;
    z-index: 100;
    color: white;
}
.answer{
    position: absolute;
    z-index: 100;
    top: 50%;
    left: 50%;
    margin-right: -50%;
    transform: translate(-50%, -50%);
}
.spinner-container{
    position: absolute;
    z-index: 100;
    top: 50%;
    left: 50%;
    margin-right: -50%;
    transform: translate(-50%, -50%);
}